.Header {
    position: fixed;
    width: 100%;
    padding: 50px 0;
    z-index: 100;
    transition: 0.8s cubic-bezier(0.2, 0.8, 0.2, 1)
}

.HeaderScrolled {
    background: rgba(0, 0, 0, 0.8);
    padding: 20px 0;
    backdrop-filter: blur(20px);
}

.HeaderGroup {
    max-width: 700px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(4, auto);
    align-items: center;
    justify-items: center;
}
.HeaderGroup img{
    width:30px;
    height:30px
}

.Header span {
    color: white;
    font-weight: 700;
    cursor: pointer;
    font-size: 19px;

}

@media (max-width:640px) {
    .Header {
        padding: 15px 0;
    }

    .HeaderGroup {
        grid-template-columns: repeat(4, auto)
    }

    .Header a:nth-child(4) {
        display: none;
    }
}